body {
  @font-face {
    font-family: "my-font";
    src: url("/fonts/Grandstander-Regular.ttf") format("woff2");
  }
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.navbar.navbar-default.myNavBar {
  color: #1c275f;
  /* background-color: #1976d2; */
  background-repeat: no-repeat;
  background-attachment: url('/images/bkg.jpg');
}

.navbar.navbar-default.myNavBar a {
  color: white;
}

.navbar.navbar-default.myNavBar a.contact {
  color: #1c275f;
}

.navbar.navbar-default.myNavBar .MuiDrawer-root a{
  color: black;
}

.nav.navbar-nav>li:not(:first-child) {
padding-left: 20px; 
}

.nav.navbar-nav.myNavBar-nav {
  flex-direction: row;
  display: inline-flex;
}

.container-fluid.main-container {
  background :lightgray;
  min-height: 100vh;
  padding-top: 20px;
  font-family: "my-font", 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  overflow: overlay;
}

.container-fluid.main-container .card {
  float: left;
  margin-left: 20px;
  flex-direction: row;
  height: 12rem;
  margin-top: 20px;

}

.btn.btn-bottom-aligned{
  position: absolute;
  bottom: 5px;
}
.text-small {
  font-size: 14px;
  font-weight: bold;
  color : white;
}

.navbar-brand.myDetails {
  display: block;
  line-height: 0.5;
}

.big-plus {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 60px;
  color: black;
  padding-top: 40px;
  margin-left: 40px;
  font-weight: bold;
}

@media only screen and (max-width: 600px) {
  body {
    background-color: lightblue;
  }
}


.m2-top {
  margin-top: 16px;
}

.mainInfo {
  display: flex;
  flex-direction: row;
}
.profileInfo  {
  color: #1c275f;
}
.profileImage {
  width: 60%;
}

.p-borderded {
  border-bottom: 1px solid;
  border-style: dashed;
}
.MuiListItem-root > a {
  display: flex;
}
.home-container {
  padding: 0;
  margin: 0 auto !important;
}

.main-content {
  margin: 0 auto;
  font-size: 22px;
}

.main-content h1 {
  color: #E91E63;;
}

.contact-color {
  color: #E91E63;
}

.MuiAppBar-colorPrimary
{
background-color: #b71558 !important;
}

.MuiTypography-body1 {
  font-size: 1.4rem !important;
}